import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'sortBy'
})
export class SortByPipe implements PipeTransform {

  transform(array: Array<string>, args: any): Array<string> {
    if(array) {
    	array.sort((a: any, b: any) => {
	    	if(a[args] < b[args]) {
	    		return -1;
	    	} else if(a[args] > b[args]) {
	    		return 1;
	    	} else {
	    		return 0;
	    	}
	    });
    }
    return array;
  }
  
}



